.page-container {
  min-height: 100vh;
  background-color: #2c2a2a;
}

.section-hero .hero-symbol {
  position: absolute;
  width: 296px;
  height: 327px;
  left: 50%;
  top: 50%;
  margin-top: 136px;
  transition: margin-top 2s ease-in-out;
}
.section-hero .hero-symbol.move {
  margin-top: 0px;
}
.section-hero .hero-symbol img {
  width: 100%;
}
.section-hero .circle {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: var(--dl-color-primary-100);
  top: 50%;
  left: 50%;
  margin-left: -20px;
  margin-top: -20px;
}
.section-hero .page-content {
  width: 684px;
  padding-top: 140px;
}
.section-hero .main-title {
  width: 430px;
}
.section-hero figure {
  width: 385px;
  height: 100%;
  overflow: hidden;
  border-radius: 30px;
}
.section-hero .snippet {
  width: 400px;
  background: linear-gradient(
    94.36deg,
    #181717 72.98%,
    rgba(24, 23, 23, 0) 100.55%
  );
  padding: 10px 16px;
  font-family: Inter;
}
.section-hero .snippet * {
  font-family: Inter;
}
.section-hero .github-corner .octo-arm {
  transform-origin: 47px 35px;
}

.section-hero .github-corner:hover .octo-arm {
  animation: octocat-wave 560ms ease-in-out;
}

@keyframes octocat-wave {
  0%,
  100% {
    transform: rotate(0);
  }
  20%,
  60% {
    transform: rotate(-25deg);
  }
  40%,
  80% {
    transform: rotate(10deg);
  }
}

@media (max-width: 500px) {
  .github-corner:hover .octo-arm {
    animation: none;
  }

  .github-corner .octo-arm {
    animation: octocat-wave 560ms ease-in-out;
  }
}

.section-work .page-overview {
  margin-top: 60px;
  margin-bottom: 65px;
  padding-right: 60px;
}
.section-work .mac-root {
  top: 50%;
  left: 50%;
  position: absolute;
  margin-left: 0;
  margin-top: 25px;
  will-change: auto;
}
.section-work .mac-root .macbook {
  width: 100%;
  position: relative;
}
.section-work .browser-container {
  position: absolute;
  top: 4.2%;
  left: 10.5%;
  width: 79.5%;
  height: 87.5%;
  display: flex;
  flex-direction: column;
  z-index: 100;
}
.section-work .browser-header {
  height: 16.5%;
  display: flex;
  flex-direction: column;
}
.section-work .browser-url {
  flex: 1;
  padding-left: 17%;
  display: flex;
  align-items: center;
}
.section-work .browser-url > div {
  opacity: 0;
  position: absolute;
  color: #000;
}
.section-work .browser-tabs {
  height: 50%;
  padding-left: 12%;
  display: flex;
}
.section-work .browser-tabs .tab {
  width: 18%;
  height: 70%;
  margin-top: 1.8%;
  color: white;
  font-size: small;
  position: relative;
  opacity: 0;
}
.section-work .browser-tabs .tab::before {
  content: '';
  width: 1px;
  height: 40%;
  top: 30%;
  left: 0;
  display: block;
  position: absolute;
  background-color: #fff;
}
.section-work .browser-tabs .tab:first-child::before {
  display: none;
}
.section-work .browser-tabs .tab.with-shape {
  width: 22%;
  background: url('/playground_assets/browsertab.svg') no-repeat;
  background-size: 100%;
}
.section-work .browser-tabs .tab.with-shape::before {
  display: none;
}
.section-work .browser-tabs .tab.with-shape + .tab::before {
  display: none;
}
.section-work .browser-body {
  flex: 1;
}
.section-work .browser-page {
  opacity: 0;
  position: absolute;
}
.section-work .browser-page .overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  transition: opacity 0.2s ease-in;
}
.section-work .browser-page .overlay:hover {
  opacity: 1;
}
