.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transition: opacity ease 0.2s;
  will-change: transform;
  opacity: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.2);
}

.modal.modal-md .modal-dialog {
  width: 800px;
  height: 600px;
}

.modal.modal-lg .modal-dialog {
  width: 1000px;
  height: 800px;
}

.modal-dialog {
  background: var(--dl-color-secondary-100);
  position: relative;
  padding-top: 50px;
}

.modal-header {
  height: 50px;
  background-color: var(--dl-color-primary-100);
  padding: 0 25px;
  left: -20px;
  position: absolute;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal.modal-lg .modal-header {
  width: 1040px;
}

.modal.modal-md .modal-header {
  width: 840px;
}

.modal-title {
  color: var(--dl-color-secondary-100);
  font-weight: lighter;
}

.modal-header .close {
  cursor: pointer;
}

.modal-container {
  padding: 40px 60px;
}

.modal-content {
  height: 100%;
  overflow: auto;
}
