.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border-width: 1px;
  border-radius: 40px;
  font-family: Outfit;
  cursor: pointer;
}
.btn-primary {
  background-color: var(--dl-color-primary-100);
  color: var(--dl-color-secondary-100);
}
.btn-primary:hover,
.btn-outline-primary {
  border: 1px solid var(--dl-color-primary-100);
  background-color: transparent;
  color: #fff;
}
.btn-info {
  background-color: var(--dl-color-info-100);
  color: var(--dl-color-secondary-100);
}
.btn-error {
  background-color: var(--dl-color-red-100);
  color: var(--dl-color-secondary-100);
}
.btn-sm {
  padding: 8px 9px;
  font-size: 12px;
}
.btn-lg {
  padding: 13px 23px;
  font-size: 18px;
}

